'use strict';

function preview(scriptpath, letter) {
  var create = null,
      game = null,
      loaded = false,
      loading = false;

  var loadscript = function loadscript(cb) {
    if (!loaded && !loading) {
      loading = true;
      $.getScript(scriptpath).done(function (script, textStatus) {
        loaded = true;
        loading = false;
        cb();
      }).fail(function (jqxhr, settings, exception) {
        loading = false;
      });
    } else {
      cb();
    }
  };

  var load = function load() {
    $('#card-letter-' + letter).hover(function () {
      if (Foundation.MediaQuery.current != "small") {
        loadscript(function () {
          if (!create) {
            create = require('pages/learn/abc/' + letter + '.js');
          }

          if (!game) {
            game = create();
            $('#learning-game-' + letter).show();
          }
        });
      }
    }, function () {
      $('#learning-game-' + letter).fadeOut({
        complete: function complete() {
          if (game) {
            game.destroy();
            game = null;
          }
        }
      });
    });
  };

  $(document).ready(function () {
    load();
  });
}

module.exports = preview;
